<template>
  <section class="editor-module-wrap">
    <Toolbar style="border-bottom: 1px solid #ccc" :editor="editorRef" :defaultConfig="toolbarConfig" :mode="mode"></Toolbar>
    <Editor style="height: 500px; overflow-y: hidden" v-model="valueHtml" :defaultConfig="editorConfig" :mode="mode" @onCreated="handleCreated"></Editor>
  </section>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css';
import { onBeforeUnmount, ref, shallowRef, onMounted, watch } from 'vue';
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
import MD5 from 'js-md5';

export default {
  name: 'editor-module',
  components: { Editor, Toolbar },
  props: {
    contentData: {
      type: Object,
      default() {
        return {};
      }
    },
    token: String,
    setContent: {
      type: Function,
      default() {
        return 'Default function';
      }
    }
  },
  setup(props, context) {
    //  shallowRef
    const editorRef = shallowRef();

    function uploadImageApiUrl() {
      let nonce = String(Math.random()).substring(2); // 
      let ts = new Date().getTime(); // 
      let s = MD5(`/api/upload/image~${nonce}${ts}`); // 

      return `/api/upload/image?nonce=${nonce}&ts=${ts}&s=${s}`;
    }

    //  HTML
    const valueHtml = ref(props.contentData.content);

    //  ajax 
    onMounted(() => {
    });

    const toolbarConfig = {
      toolbarKeys: ["uploadImage", "clearStyle"]
    };
    const editorConfig = {
      placeholder: '...', MENU_CONF: {
        uploadImage: {
          headers: {
            Authorization: props.token, //  Authorization 
            ['X-Void-Risk']: 1 // 
          },
          server: uploadImageApiUrl(),
          fieldName: 'news-info'
        }
      }
    };

    // 
    onBeforeUnmount(() => {
      const editor = editorRef.value;
      if (editor == null) return;
      editor.destroy();
    });

    const handleCreated = (editor) => {
      editorRef.value = editor; //  editor 
    };

    watch(valueHtml, (CValue) => {
      context.emit('setContent', CValue);
    })

    return {
      editorRef,
      valueHtml,
      mode: 'default', //  'simple'
      toolbarConfig,
      editorConfig,
      handleCreated
    };
  }
};
</script>

