<template>
  <section class="news-manage-wrap">
    <section class="head-box">
      <el-input v-model="headForm.keyword" placeholder="" clearable></el-input>
      <p class="label-text"></p>
      <el-select v-model="headForm.type">
        <el-option v-for="item in headNewsTypeGroup" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-button type="primary" @click="headSearchButton"></el-button>
      <el-button type="warning" @click="headResetButton"></el-button>
      <el-button type="success" @click="headAddButton"></el-button>
    </section>

    <section class="table-box">
      <el-table size="default" :data="tableData" border style="width: 100%" empty-text="" @cell-click="tableCellClick">
        <el-table-column header-align="center" align="center" label="#" width="60">
          <template #default="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column header-align="center" align="center" label="" width="60">
          <template #default="scope">{{ newsType(scope.row) }}</template>
        </el-table-column>
        <el-table-column header-align="center" align="left" prop="title" label="" width="300">
          <template #default="scope">
            <p class="single-line-show" :title="scope.row.title">{{ scope.row.title }}</p>
          </template>
        </el-table-column>
        <el-table-column header-align="center" align="left" prop="content" label="">
          <template #default="scope">
            <p class="single-line-show">{{ scope.row.content }}</p>
          </template>
        </el-table-column>
        <el-table-column header-align="center" align="center" prop="edit_time" label="" width="170">
          <template #default="scope">{{ formatDate(scope.row.edit_time) }}</template>
        </el-table-column>
        <el-table-column header-align="center" align="center" prop="create_time" label="" width="170">
          <template #default="scope">{{ formatDate(scope.row.create_time) }}</template>
        </el-table-column>
        <el-table-column header-align="center" align="center" label="" width="200">
          <template #default="scope">
            <el-button type="primary" @click="rowEditButton(scope)"></el-button>
            <el-popconfirm confirm-button-text="" cancel-button-text="" icon-color="red" :title="'' + scope.row.title + '?'" @confirm="rowDeleteButton(scope.row)">
              <template #reference>
                <el-button type="warning"></el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </section>

    <el-dialog v-model="editDialogVisible" :close-on-click-modal="false" :destroy-on-close="true" custom-class="edit-dialog">
      <el-form label-width="80px" :model="tableRowData">
        <el-form-item label=":">
          <el-select v-model="tableRowData.type" placeholder="">
            <el-option v-for="item in newsTypeGroup" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="tableRowData.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" class="upload-image">
          <el-upload class="avatar-uploader" :action="uploadImageUrl" :headers="uploadImageHeaders" name="news-info" :show-file-list="false" list-type="picture" :on-success="imageUploadSuccess" :before-upload="imageUploadBefore">
            <img v-if="tableRowData.thumbnail" :src="tableRowData.thumbnail" class="image" />
            <el-icon v-else class="avatar-uploader-icon">
              <Plus></Plus>
            </el-icon>
          </el-upload>
          <p class="info"></p>
        </el-form-item>
        <el-form-item label=":">
          <section class="editor-box">
            <editor-module :contentData="tableRowData" :token="token" @setContent="childModuleSetTableRowData"></editor-module>
          </section>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editDialogVisible = false"></el-button>
          <el-button type="primary" @click="addDialogButton" v-if="dialogMode === 'add'"></el-button>
          <el-button type="primary" @click="editDialogButton" v-else></el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog v-model="detailDialogVisible" :destroy-on-close="true" custom-class="show-dialog">
      <h3 class="title">{{ tableRowDetailData.title }}</h3>
      <section class="detail-wrap">
        <article class="detail-content" v-html="tableRowDetailData.content"></article>
      </section>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="detailDialogVisible = false"></el-button>
        </span>
      </template>
    </el-dialog>
  </section>
</template>

<script>
import MD5 from 'js-md5';
import { mapGetters } from 'vuex';
import { ElMessage } from 'element-plus';
import { formatTargetDate } from '@/tools/tools.js';
import { newsTypeList, addNews as addTableRowApi, newsList as getTableDataApi, editNews as editTableRowApi } from '@/api/api.js';
import editorModule from '@/components/common/editor-module.vue';

export default {
  name: 'news-manage',
  components: { editorModule },
  props: {},
  data() {
    return {
      headForm: { keyword: '', type: 0 },
      tableData: [],
      tableRowData: {},
      dialogMode: '',
      editDialogVisible: false,
      tableRowDetailData: {},
      detailDialogVisible: false,
      newsTypeGroup: [],
      editor: null
    };
  },
  watch: {},
  computed: {
    ...mapGetters(['token']),
    headNewsTypeGroup() {
      return [].concat([{ label: '', value: 0 }], this.newsTypeGroup);
    },
    uploadImageHeaders: function () {
      return {
        Authorization: this.token, //  Authorization 
        ['X-Void-Risk']: 1 // 
      };
    },
    uploadImageUrl: function () {
      let nonce = String(Math.random()).substring(2); // 
      let ts = new Date().getTime(); // 
      let s = MD5(`/api/upload/image~${nonce}${ts}`); // 

      return `/api/upload/image?nonce=${nonce}&ts=${ts}&s=${s}`;
    }
  },
  beforeCreate() {
    /**
     * /
     */
  },
  created() {
    /**
     * 
     * /
     *  el property 
     */
    this.getNewsTypeList();
    this.getTableData();
  },
  beforeMount() {
    /**
     *  render 
     * 
     */
  },
  mounted() {
    /**
     *  app.mount  vm.el 
     *  mounted  vm.el 
     *  mounted  mounted  vm.nextTick
     * 
     */
  },
  beforeUpdate() {
    /**
     * DOM 
     *  DOM 
     * 
     */
  },
  updated() {
    /**
     *  DOM 
     *  DOM  DOM 
     * updated  updated  vm.nextTick
     */
  },
  beforeUnmount() {
    /**
     * 
     * 
     */
  },
  unmounted() {
    /**
     *  Vue 
     * 
     */
  },
  methods: {
    // 
    getNewsTypeList() {
      const _this = this;

      newsTypeList().then((response) => {
        const { code, data, message } = response.data;

        if (code === 200) {
          _this.newsTypeGroup = data;
        } else {
          ElMessage.error(message);
        }
      });
    },
    // 
    getTableData() {
      const _this = this;

      getTableDataApi(_this.headForm).then((response) => {
        const { code, data, message } = response.data;

        if (code === 200) {
          _this.tableData = data;
        } else {
          ElMessage.error(message);
        }
      });
    },
    // 
    formatDate(date) {
      let targetTimeObject = formatTargetDate(date);
      return `${targetTimeObject.YY}-${targetTimeObject.MM}-${targetTimeObject.DD} ${targetTimeObject.HH}:${targetTimeObject.mm}:${targetTimeObject.ss}`;
    },
    // -
    newsType(row) {
      return this.newsTypeGroup.filter((element) => row.type === element.value)[0].label;
    },
    // 
    headSearchButton() {
      this.getTableData();
    },
    // 
    headResetButton() {
      this.headForm = { keyword: '', type: 0 };
    },
    // 
    headAddButton() {
      this.tableRowData = { title: '', content: '', thumbnail: '', type: '' };
      this.dialogMode = 'add';
      this.editDialogVisible = true;
    },
    // -
    tableCellClick(row, column) {
      if (column.property === 'title' || column.property === 'content') {
        this.tableRowDetailData = { title: row.title, content: row.content };
        this.detailDialogVisible = true;
      }
    },
    // ---
    rowEditButton(scope) {
      this.tableRowData = { index: scope.$index, ...scope.row };
      this.dialogMode = 'edit';
      this.editDialogVisible = true;
    },
    // ---
    rowDeleteButton(row) {
      const _this = this;

      editTableRowApi({ op_type: 0, id: row.id }).then((response) => {
        const { code, message } = response.data;

        if (code === 200) {
          ElMessage.success(message);
          _this.getTableData();
        } else {
          ElMessage.error(message);
        }
      });
    },
    // Dialog-
    addDialogButton() {
      const _this = this;
      const { title, content, type } = _this.tableRowData;

      if (!type) {
        ElMessage.error('');

        return;
      }

      if (!title) {
        ElMessage.error('');

        return;
      }

      if (!content) {
        ElMessage.error('');

        return;
      }

      addTableRowApi(_this.tableRowData).then((response) => {
        const { code, message } = response.data;

        if (code === 200) {
          ElMessage.success(message);
          _this.getTableData();
          _this.editDialogVisible = false;
        } else {
          ElMessage.error(message);
        }
      });
    },
    // Dialog-
    editDialogButton() {
      const _this = this;
      const { index, id, title, content, thumbnail, type } = _this.tableRowData;
      let postData = {
        op_type: 1,
        id: id
      };

      if (!type) {
        ElMessage.error('');

        return;
      } else if (type !== _this.tableData[index].type) {
        postData.type = type;
      }

      if (!title) {
        ElMessage.error('');

        return;
      } else if (title !== _this.tableData[index].title) {
        postData.title = title;
      }

      if (!content) {
        ElMessage.error('');

        return;
      } else if (content !== _this.tableData[index].content) {
        postData.content = content;
      }

      if (thumbnail !== _this.tableData[index].thumbnail) {
        postData.thumbnail = thumbnail;
      }

      if (Object.keys(postData).length > 2) {
        editTableRowApi(postData).then((response) => {
          const { code, message } = response.data;

          if (code === 200) {
            ElMessage.success(message);
            _this.getTableData();
            _this.editDialogVisible = false;
          } else {
            ElMessage.error(message);
          }
        });
      } else {
        ElMessage.warning('');
      }
    },
    // 
    imageUploadBefore(file) {
      let result = true;

      if (!/image\//.test(file.type)) {
        result = false;
        ElMessage.error('');
      }

      return result;
    },
    // 
    imageUploadSuccess(response) {
      const { code, data, message } = response;

      if (code === 10004) {
        ElMessage.error(message);
        this.$router.push('/');
      } else if (code === 200) {
        this.tableRowData.thumbnail = data.url;
        ElMessage.success(message);
      } else {
        ElMessage.error(message);
      }
    },
    //    content
    childModuleSetTableRowData(content) {
      this.tableRowData.content = content.replace(/(<p)(\s+?[\s|\S]+?)(>)/gi, '$1$3').replace(/(<br><\/br>)|(<br[\s|\S]*?>)/gi, '').replace(/(<p><\/p>)/gi, '')
    },
  }
};
</script>

