    .el-input {
      margin-right: 12px;
      width: 400px;
    }

    .el-select {
      margin-right: 12px;
      width: 100px;
    }

    .el-button:last-of-type {
      float: right;
    }

    .label-text {
      display: inline-block;
      margin-right: 10px;
      font-size: 14px;
