      overflow: hidden;

      &:hover {
        color: #409eff;
      }
    }
  }

  :deep {
    .edit-dialog {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: 0;
      transform: translate(-50%, -50%);
      width: 1200px;

      .el-dialog__body {
        .el-form {
          .el-form-item:last-of-type {
            margin-bottom: 0;
          }

          .upload-image {
            .el-form-item__label {
              line-height: 90px;
            }

            .image {
              width: 160px;
              height: 90px;
              vertical-align: top;
            }

            .avatar-uploader {
              width: 160px;
              height: 90px;
              border: 1px dashed rgb(205, 208, 214);

              &:hover {
                border-color: #79bbff;
              }

              .el-upload {
                width: 100%;
                height: 100%;
              }
            }

            .info {
              margin-left: 15px;
              color: #606266;
            }
          }

          .editor-box {
            width: 100%;
            max-height: 800px;
            border: 1px solid #000;
          }
        }
      }
    }

    .show-dialog {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: 0;
      transform: translate(-50%, -50%);
      width: 1200px;

      .el-dialog__body {
        .title {
          padding: 5px 0 10px;
          font-size: 20px;
          text-align: center;
        }

        .detail-wrap {
          max-height: 500px;
          overflow-y: auto;

          .detail-content {
            font-size: 0;

            p {
              margin-bottom: 10px;
              font-size: 16px;
              width: 100%;
              line-height: 1.5;
              text-indent: 2em;

              &:last-of-type {
                margin-bottom: 0;
              }

              img {
                display: block;
                margin: 0 auto;
                max-width: 100%;
              }
            }
          }
        }
      }
    }
  }
}
</style>
